/*  */
<template>
  <Modal v-model="isShow" width="360" title="">
    <p slot="header" style="color:green; text-align:center">
      <Icon type="information-circled"></Icon>
      <span></span>
    </p>
    <div style="text-align:center; font-size: 12px;">
      <Row style="margin-top: 10px; margin-bottom: 15px;">
        <Col span="8"></Col>
        <Col span="11"></Col>
        <Col span="5"></Col>
      </Row>
      <Row v-for="user in users" :key="user.id" style="height: 23px;">
        <Col span="2">
          <Icon type="ios-person" size="18" v-if="user.relationType == 'OWNER'"></Icon>
          <Icon type="ios-person-outline" size="18" v-else></Icon>
        </Col>
        <Col span="6">{{user.name}}</Col>
        <Col span="11">{{common.revertTel(user.tel)}}</Col>
        <Col span="5" v-if="user.relationType == 'OWNER'"></Col>
        <Col span="5" v-else></Col>
      </Row>
    </div>
    <div slot="footer">
      <Button type="info" size="large" long @click="userModal = false"></Button>
    </div>
  </Modal>

</template>

<script>
  import common from '../libs/common';
  import server from '../config/api';


  export default {
    name: 'UserModal',
    // 
    // sensorDetail; sensorEvent
    // sensorEventModal: modaldata
    props: [ 'userModal','sensorId'],
    data() {
      return {
        common: common,
        isShow: false,
        users:[]
      }
    },
    methods: {
      closeModal: function () {
        this.$emit('closeUserModal');
      }
    },
    watch: {
      isShow: function(val, oldVal){
        if (val == true) {
          // do something
          this.$http.get(server.api.monitor_center.sensor.findSensorUsers.replace('{sensorId}',this.sensorId)).then((response)=>{
              this.users = response.body.data;
          },(error)=>{
              this.$Notice.error({title:''});
          });
        } else {
          // modaltrue
          this.closeModal();
        }
      },
      // modal
      userModal: function (val, oldVal) {
          this.isShow = val;
      }
    }
  }

</script>



