<script>
  import config from '../config/config';
  import server from "../config/api";
  import util from '../libs/util';
  import Vue from 'vue';
  import common from '../libs/common';
  import SensorEventModal from '../components/process-event.vue';
  import {tryMove} from "../libs/modalMove.js";
  import ol from 'openlayers';
  import niceScroll from 'jquery.nicescroll';

  export default {
    name: 'DeviceModal',
    /**
     * selectEvent; MonitorCenterDeviceEvent
     * buildingId  deviceId  eventSource('GATEWAY', 'DEVICE')
     */
    props: ['selectedEvent','deviceDetailModal'],
    data() {
      return {
        obj: {},
        common: common,
        self: this,
        currentActiveTab: 'planForm',
        sensorEventsModal: false,
        cameraDetailModal: false,
        cameraType: undefined,
        cameraUrl: undefined,
        cameraCurrentUrl: undefined,
        processComment: undefined,
        // 
        type: 'device',
        isShow: false,
        deviceDetail: {},
        modalTitle: ' ',
        deviceEventList: [],
        showCamera: false,
        eventPageTotal:undefined,
        eventPageNum: 1,
        eventPageSize: 20,
        currentEvent: {},
        eventColumns: [
          {
            title: '',
            align: 'center',
//            width: 85,
            render:(h,params)=>{
              let obj = this.selectEventStatus(params.row.eventType);
              return h('div',[
                h('div',{
                  style:{
                    color: obj.color
                  }
                }, obj.value)
              ]);
            }
          },
          {
            title: '',
            align: 'center',
            width: 120,
            render:(h,params)=>{
              let dateTime=util.dateFormat(new Date(parseInt(params.row.creationDt)),'MM-dd HH:mm');
              return h('div',[
                h('div',{

                },dateTime)
              ]);
            }
          },
          {
            title: '',
            align: 'center',
            render:(h,params)=>{
              let status=undefined;
              let color=undefined;
              if(params.row.processStatus == 'OPEN') {
                status="",color="#ff3535";
              }else if(params.row.processStatus == 'PENDING') {
                status="",color="#02c3a5";
              }else if(params.row.processStatus == 'CLOSE') {
                status="",color="#1fc328";
              }
              return h('div',[
                h('div',{
                  style:{
                    color:color
                  }
                },status)
              ]);
            }
          },
          {
            title: '',
            align: 'center',
            render:(h,params)=>{
              if(params.row.processConfirmation === null) {
                return h('span', '');
              }else if(params.row.processConfirmation === 'REAL') {
                return h('span', {
                  style: {
                    color: '#d43c33'
                  }
                }, '');
              }else if(params.row.processConfirmation === 'FAKE') {
                return h('span', '');
