    methods: {
      initData: function(){
        if(this.selectedEvent.eventSource === 'DEVICE') {
          // 
          this.loadDeviceInfo();
          this.loadDeviceCamera(this.selectedEvent.deviceId, this.selectedEvent.buildingId);
          this.loadDeviceEvents(this.selectedEvent.deviceId, this.selectedEvent.buildingId, 1);
        } else if(this.selectedEvent.eventSource === 'GATEWAY') {
          // 
          this.loadGateWayInfo();
          this.loadCheckDutyList(1);
          this.loadGateWayEvents(1);
        }
        Vue.nextTick(() => {
          $(".auto-height-table .ivu-table-body").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      },
      loadDeviceInfo: function() {
        this.$http.get(server.api.organization.device.get.replace('{buildingId}',this.selectedEvent.buildingId)
          .replace('{deviceId}',this.selectedEvent.deviceId)).then((response)=>{
          this.deviceDetail = response.body.data;
          this.deviceDetail.sourceType = 'DEVICE';
          if(this.deviceDetail.sensorUsers !== undefined
            && this.deviceDetail.sensorUsers !== null && this.deviceDetail.sensorUsers.length !== 0) {
            this.deviceDetail.sensorUsers = _.sortBy(this.deviceDetail.sensorUsers, 'relationType');
          }
          this.modalTitle = this.deviceDetail.deviceName + '-' + this.deviceDetail.typeName;
          // 
          Vue.nextTick(() => {
            let container = $("#device_container");
            let noPlanImg = $(".no-plan-img");
            if(this.deviceDetail.planformUrl === undefined || this.deviceDetail.planformUrl === null) {
              // 
              container.empty();
              container.addClass("no-plan");
              noPlanImg.css("display", "block");
            } else {
              container.removeClass("no-plan");
              noPlanImg.css("display", "none");
              this.drawMap(server.api.img.replace('{imageId}', this.deviceDetail.planformUrl));
            }
          });
        },(error)=>{
          this.$Message.error('');
        });
      },
      loadGateWayInfo: function() {
//        let buildingId = this.selectedEvent.buildingId;
//        let tranceiverId = this.selectedEvent.tranceiverId;
        this.$http.get(server.api.organization.gateway.getInfo.replace('{gatewayId}', this.selectedEvent.deviceId)
            + '?buildingId=' + this.selectedEvent.buildingId).then((response) => {
          this.deviceDetail = response.body.data;
          this.deviceDetail.sourceType = 'GATEWAY';
          this.modalTitle = '-' + this.deviceDetail.name;
        }, (error) => {
          this.$Message.error("");
        });
      },
      loadCheckDutyList: function(pageNum) {
        let buildingId = this.selectedEvent.buildingId;
        let gatewayId = this.selectedEvent.deviceId;
        this.$http.get(server.api.organization.gateway.checkDuty.replace('{gatewayId}', gatewayId)
            + '?buildingId=' + buildingId + '&pageNum=' + pageNum + '&pageSize=' + this.checkDutyPageSize).then((response) => {
          this.checkDutyPageTotal = response.body.data.total;
          this.checkDutyList = response.body.data.rows;
          Vue.nextTick(() => {
            $(".check-duty-table.auto-height-table .ivu-table-body").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "7px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        }, (error) => {
          this.$Message.error("");
        });
      },
      loadDeviceCamera: function(deviceId, buildingId) {
        this.$http.get(server.api.organization.device.camera.getBaseCamerasByDevice.replace('{deviceId}', deviceId)
            + '?buildingId=' + buildingId).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          this.cameraList =response.body.data;
          Vue.nextTick(() => {
            $("#camera_list").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "4px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        }, (error) => {
          this.$Message.error("");
        });
      },
      loadGateWayEvents: function(pageNum) {
        let buildingId = this.selectedEvent.buildingId;
        let gatewayId = this.selectedEvent.deviceId;
        this.$http.get(server.api.organization.event.gateway + '?level=GATEWAY&buildingId=' + buildingId + '&gatewayId=' + gatewayId + '&pageSize=' + this.eventPageSize + '&pageNum=' + pageNum).then((response) => {
          this.eventPageTotal = response.body.data.total;
          this.deviceEventList = response.body.data.rows;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        }, (error) => {
          this.$Message.error("");
        });
      },
      loadDeviceEvents: function(deviceId, buildingId, pageNum){
        this.$http.get(server.api.organization.event.get + '?level=DEVICE&deviceId=' + deviceId + '&buildingId=' + buildingId + '&pageSize=' + this.eventPageSize+ '&pageNum=' + pageNum).then((response) => {
          this.eventPageTotal = response.body.data.total;
          this.deviceEventList = response.body.data.rows;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        }, (error) => {
          this.$Message.error("");
        });
      },
      showDeviceCamera: function(cameraDetail) {
        if(cameraDetail.extInfo === null || cameraDetail.extInfo === undefined) {
          return;
        }
        this.cameraType = cameraDetail.extInfo.cameraType;
        this.cameraDetailModal = true;
        setTimeout(() => {
          if(this.cameraType === 'YDPNP') {
            this.ShowDev(cameraDetail.extInfo.cameraUrl, cameraDetail.extInfo.cameraUid, cameraDetail.extInfo.cameraPwd);
          } else if(this.cameraType === 'YS7') {
            this.cameraUrl = cameraDetail.extInfo.cameraUrl;
            this.$http.get(server.api.organization.device.camera.getWholeUrl + '?url=' +
              this.cameraUrl + '&type=' + this.cameraType).then((response) => {
              if(!response.body.meta.success) {
                this.$Message.error("");
                return;
              }
              this.genSingleVLCObjHtml(
                this.getUUID(),
                'camera_vlc_video',
                (response.body.data === null || response.body.data.length === 0) ? '' : response.body.data[0],
                $('#camera_vlc_video').width(),
                $('#camera_vlc_video').height() - 4
              );
            }, (error) => {
              this.$Message.error("");
            });
          } else if(this.cameraType === 'STREAM') {
            this.cameraUrl = cameraDetail.extInfo.cameraUrl;
            this.genSingleVLCObjHtml(
              this.getUUID(),
              'camera_vlc_video',
              this.cameraUrl,
              $('#camera_vlc_video').width(),
              $('#camera_vlc_video').height() - 4
            );
          }
        }, 500);
      },
      getUUID: function() {
        return 'xxxxxxxx_xxxx_4xxx_yxxx_xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
          let r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
          return v.toString(16);
        });
      },
      // VLC
      genSingleVLCObjHtml: function(index, containerId, mrl, width, height) {
        console.log("width = " + width + ', height = ' +  height);
        let id = "vlc_" + index;
        if (!!window.ActiveXObject || "ActiveXObject" in window) { // IE
          $("#" + containerId).html("<object id='" + id + "' classid='clsid:9BE31822-FDAD-461B-AD51-BE1D1C159921' codebase='http://downloads.videolan.org/pub/videolan/vlc/latest/win32/axvlc.cab'><param name='mrl' value='" + mrl + "'></param></object>");
          $("#" + id).css("width", width);
          $("#" + id).css("height", height);
        } else if (navigator.userAgent.indexOf("Firefox") > -1) { // Firefox
          $("#" + containerId).html("<embed id='" + id + "' type='application/x-vlc-plugin' mrl='" + mrl + "' width='" + width + "' height='" + height + "'></embed>");
        } else {
          this.$Message.error("")
        }
      },

      ShowDev: function (szDevIP, szAuthAcc, szAuthPwd) {
        let VER_AX_LOCALE = "1.1.15.174";//the version of the ActiveX
        let nPort = 81; //the HTTP port of ip camera, if the szDevIP is serial ID, this whill be ignore
        let nImgW=320,nImgH=240;//the window size of ip camera.
        let szDevName="";//the caption shown on the video
        let nDevType=922;
        let bInLan=1;//whether in LAN,1:LAN, 0WAN
        let remote = document.querySelector('#remote');
        try {
          remote.ClearDevs();
        }
        catch (exception) {
          return;
        }

        // remote.ShowTitle = 0;
        remote.Lan = "cn";  //language cn:chinese en:english jp:japanese
        remote.ShowOSDName = 1;//whether to show ipcam caption on the video
        remote.SwitchLayout("1 x 1");
        remote.Width = nImgW;  //width of the window
        remote.Height = nImgH; //height of the window
        // AddDev4(IP Camera mode (903:M; 902:F; 901:E; 904:V; 905:A),LAN,serial ID or IP, HTTP port, display name, account,password,channel)
        remote.ShowToolBar = 1;//whether to show toolbar
        remote.AddDev4(nDevType, bInLan, szDevIP, nPort, szDevName, szAuthAcc, szAuthPwd, 1);
        remote.Listen = 1;//whether to play soound
        remote.TurnImg = 0;   //whether reverse video
        remote.ConnectAll();
        remote.Start();
      },
      showProcess: function(eventId) {
        this.$http.get(server.api.organization.event.detail.replace('{eventId}', eventId)).then((response) => {
          this.currentEvent = response.body.data;
          this.currentEvent.colorObj = this.selectEventStatus(this.currentEvent.eventType);
          this.type = 'event';
          Vue.nextTick(() => {
            $(".log-list").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
//              railpadding: { top:10, right:-11, left:0, bottom:-10 },
              cursorwidth: "7px",
              cursoropacitymax: 0.4,
              cursorborder: "", // css definition for cursor border
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        }, (error) => {
          this.$Message.error("");
        });
      },
      tranceiverInstruction: function(param) {
        let title;
        if(param === 'MC_OP_GW_INSPECTION') {
          title = '';
        } else if(param === 'MC_OP_GW_DUTY_CHECK') {
          title = '';
        }
        this.$Modal.confirm({
          title: title,
          content: '<p>' + title + '</p>',
          onOk: () => {
            this.$http.post(server.api.organization.gateway.instruct.replace('{gatewayId}', this.selectedEvent.deviceId)
                + '?type=' + param).then((response) => {
              if(param === 'MC_OP_GW_INSPECTION') {
                this.loadGateWayInfo();
              } else if (param === 'MC_OP_GW_DUTY_CHECK') {
                this.loadCheckDutyList(1);
                this.checkDutyLoading = true;
                let self = this;
                // 
                setTimeout(() => {
                  self.checkDutyLoading = false;
                }, 10 * 60 * 1000);
              }
              this.$Message.success("");
            }, (error) => {
              this.$Message.error("");
            });
          },
          onCancel: () => {
          }
        });
      },
      clickTranceiverTab: function(tabName) {
        // workingStatus
        if(tabName === 'checkDuty') {
          setTimeout(() => {
            $(".check-duty-table.auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        }
      },
      revertTranceiverStatus: function(param) {
        this.$Modal.confirm({
          title: '',
          content: '<p></p>',
          onOk: () => {
            // 
            let requestParam = {
              gatewayId: this.selectedEvent.deviceId
            };
            if(param === 'all') {
              requestParam.stManualAlarm = 'N';
              requestParam.stFire = 'N';
              requestParam.stFault = 'N';
              requestParam.stPPowerFault = 'N';
              requestParam.stSPowerFault = 'N';
              requestParam.stOffline = 'N';
              requestParam.stWireBreak = 'N';
            } else {
              switch (param) {
                case 'stManualAlarm':
                  requestParam.stManualAlarm = 'N';
                  break;
                case 'stFire':
                  requestParam.stFire = 'N';
                  break;
                case 'stFault':
                  requestParam.stFault = 'N';
                  break;
                case 'stPPowerFault':
                  requestParam.stPPowerFault = 'N';
                  break;
                case 'stSPowerFault':
                  requestParam.stSPowerFault = 'N';
                  break;
                case 'stOffline':
                  requestParam.stOffline = 'N';
                  break;
                case 'stWireBreak':
                  requestParam.stWireBreak = 'N';
                  break;
              }
            }
            this.$http.put(server.api.organization.gateway.revert, requestParam).then((response) => {
              this.$Message.success("");
              this.loadGateWayInfo();
            }, (error) => {
              this.$Message.error("");
            });
          },
          onCancel: () => {
          }
        });
      },
      revertStatus: function(deviceId, status) {
        this.$Modal.confirm({
          title: '',
          content: '<p></p>',
          onOk: () => {
            this.$http.put(server.api.organization.device.revertStatus, {
              deviceId: deviceId,
              status: status
            }).then((response) => {
              if(!response.body.data) {
                this.$Message.error("");
                return;
              }
              this.$Message.success("");
              this.deviceDetail.status = 'NORMAL';
            }, (error) => {
              this.$Message.error("");
            });
          },
          onCancel: () => {
          }
        });
      },
      commitProcess: function(action) {
        let param = {
          eventId: this.currentEvent.eventId,
          action: action,
          eventSource: this.currentEvent.eventSource
        };
        if(action === 'COMMENT') {
          if(this.processComment === undefined || this.processComment === null || this.processComment.length === 0) {
            this.$Message.info("");
            return;
          }
          param.comment = this.processComment;
        } else if(action === 'CLOSE_EVENT') {
          if(this.currentEvent.processConfirmation === undefined || this.currentEvent.processConfirmation === null) {
            this.$Message.info("");
            return;
          }
          param.confirmaiton = this.currentEvent.processConfirmation;
        }
//        COMMENT -- log
//        OPEN_PROCESS -- 
//        CLOSE_EVENT -- 
//        REOPEN_EVENT -- 
        this.$http.put(server.api.organization.event.process.replace('{eventId}', this.currentEvent.eventId), param).then((response) => {
          this.currentEvent = response.body.data;
          this.currentEvent.colorObj = this.selectEventStatus(this.currentEvent.eventType);
          // 
          let index = _.findIndex(this.deviceEventList, {eventId: this.currentEvent.eventId});
          if(index !== -1) {
            Vue.set(this.deviceEventList, index, this.currentEvent);
          }
          // 
          this.$emit("processOver", this.currentEvent);
          this.$Message.success("");
          this.processComment = undefined;
          Vue.nextTick(() => {
            $(".log-list").getNiceScroll().resize();
          });
        }, (error) => {
          this.$Message.error("");
        })
      },
      showExtInfo: function() {
        let result = '';
        let extInfo = this.deviceDetail.extInfo;
        if(extInfo !== undefined && extInfo !== null) {
          // 
          if(extInfo.lowBattery || extInfo.dockerOpen){
            result += '(';
            if(extInfo.lowBattery && extInfo.dockerOpen) {
              result += '';
            } else if(extInfo.lowBattery) {
              result += '';
            } else {
              result += '';
            }
            result += ')';
          }
        }
        //   1.35/2.0~4.5
        if(this.deviceDetail.analogInd === 'Y') {
          result += '' + this.deviceDetail.analogValue + this.deviceDetail.unit + '/' + this.deviceDetail.downLimit + '~' + this.deviceDetail.upLimit + this.deviceDetail.unit + '';
        }
        return result;
      },
      searchSensorEvent: function (pageNum) {
        // 
        this.loadDeviceEvents(this.selectedEvent.deviceId, this.selectedEvent.buildingId, pageNum);
      },
      // 
      drawMap: function(imgUrl) {
        let image = new Image();
        image.src=imgUrl;
        let self = this;
        image.onload = function() {
          self.drawMapWithRange(imgUrl, image.width, image.height);
        }
      },
      drawMapWithRange(imgUrl, imgWidth, imgHeight) {
        let container = $("#device_container");
        container.empty();
        this.center = ol.proj.transform([121.609643, 38.913305], 'EPSG:4326', 'EPSG:3857');
        // (minX, minY, maxX, maxY) 1000 * 719
        this.extent = [this.center[0] - imgWidth * 1000 / 2, this.center[1] - imgHeight * 1000 / 2, this.center[0] + imgWidth * 1000 / 2, this.center[1] + imgHeight * 1000 / 2];

        this.map = new ol.Map({
          view: new ol.View({
            center: this.center,
            zoom: 7
          }),
          controls: [],
          target: 'device_container'
        });
        this.map.addLayer(new ol.layer.Image({
          source: new ol.source.ImageStatic({
            url: imgUrl, // 
            imageExtent: this.extent     // 
          })
        }));
        Vue.nextTick(()=>{
          // 
          this.drawSensors();
        });
      },
      // 
      drawSensors: function () {
        // 
        let deviceList = [];
        let currentDeviceId = this.deviceDetail.deviceId;
        this.$http.get(server.api.organization.building.abnormalDevices.replace('{buildingId}', this.selectedEvent.buildingId) + '?floorId=' + this.deviceDetail.floorId).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          deviceList = response.body.data;
          // 
          let defaultPosX = this.extent[0] + 100000;
          let defaultPosY = this.extent[3] - 100000;
          this.deviceLayer = new ol.layer.Vector({
            source: new ol.source.Vector(),
            extent: this.extent,
          });
          // 
          if(this.deviceDetail.status === 'NORMAL' && this.deviceDetail.posX !== undefined && this.deviceDetail.posX !== null) {
            deviceList.push(this.deviceDetail);
          }
          for(let index in deviceList) {
            let device = deviceList[index];
            if (device.posX === undefined || device.posX === null) {
              // 
              device.posX = (defaultPosX);
              device.posY = (defaultPosY -= 50000);
            }
            if (device.deviceId === this.deviceDetail.deviceId) {
              Vue.nextTick(() => {
                this.map.setView(new ol.View({
                  center: [device.posX, device.posY],
                  zoom: 7
                }));
              });
            }
            // Feature(extentLeft + picX, extentTop + picY)
            let deviceFeature = new ol.Feature({
              geometry: new ol.geom.Point([device.posX, device.posY]),
              sensor: device
            });
            // Feature
            let color;
            let deviceSrc;
            if (device.status === 'FIRE') {
              color = '#ec3434';
              deviceSrc = this.device_fire;
            } else if (device.status === 'FAULT') {
              color = '#e86d00';
              deviceSrc = this.device_fault;
            } else if (device.status === 'WARN') {
              color = '#c2c014';
              deviceSrc = this.device_warn;
            } else if (device.status === 'SHIELD') {
              color = '#818181';
              deviceSrc = this.device_shield;
            } else if (device.status === 'START' || device.status === 'NORMAL') {
              color = '#4bbd4b';
              deviceSrc = this.device_start;
            } else if (device.status === 'FEEDBACK') {
              color = '#6ba3f6';
              deviceSrc = this.device_feedback;
            }
            deviceFeature.setStyle(new ol.style.Style({
              image: new ol.style.Icon({
                src: deviceSrc,
                style: new ol.style.Style({
                  cursor: 'poniter'
                })

              }),
              text: new ol.style.Text({
                // font: '10px sans-serif' css
                offsetY: -20,
                text: device.deviceName,
                fill: new ol.style.Fill({
                  color: color
                }),
                stroke: new ol.style.Stroke({    //
                  color: '#fff',
                  width: 3
                })

              })
            }));
            this.deviceLayer.getSource().addFeature(deviceFeature);
          }
          this.map.addLayer(this.deviceLayer);
        }, (error) => {
          this.$Message.error("");
        });
      },
      closeModal: function () {
        // 
        this.$emit('closeModal');
      },
      selectEventStatus: function (eventType) {
        let obj = {};
        switch(eventType){
          case 'NORMAL':
          case 'GW_MANUAL_ALARM_OFF':
          case 'GW_FIRE_OFF':
          case 'GW_FAULT_OFF':
          case 'GW_P_POWER_FAULT_OFF':
          case 'GW_S_POWER_FAULT_OFF':
          case 'GW_OFFLINE_OFF':
          case 'GW_WIRE_BREAK_OFF':
            obj.value=''; obj.color='#4bbd4b'; break;
          case 'FIRE':
          case 'GW_OP_MANUAL_ALARM':
          case 'GW_FIRE_ON':
            obj.value=''; obj.color='#ec3434'; break;
          case 'FAULT':
          case 'GW_FAULT_ON':
          case 'GW_P_POWER_FAULT_ON':
          case 'GW_S_POWER_FAULT_ON':
          case 'GW_OFFLINE_ON':
          case 'GW_WIRE_BREAK_ON':
            obj.value=''; obj.color='#e86d00'; break;
          case 'SHIELD': obj.value=''; obj.color='#818181'; break;
          case 'WARN': obj.value=''; obj.color='#ff6000'; break;
          case 'START': obj.value=''; obj.color='#4bbd4b'; break;
          case 'FEEDBACK': obj.value=''; obj.color='#6ba3f6'; break;
//          case 'ALARM': obj.value=''; obj.color='#d43c33'; break;
//          case 'OFFLINE': obj.value=''; obj.color='#666'; break;
        }
        return obj;
      },
//      showLog (index) {
//        // log
//        console.info(this.sensorEvents[index]);
//        this.log = this.sensorEvents[index].processLog;
//        this.sensorEventsModal = true;
//      },

      resizeDynamicListHeight: function () {
        // 
        //this.$refs.modalRightCol.childNodes.item(0).style.minHeight = ((this.$refs.modalLeftBottomCol.getBoundingClientRect().height) + 10 + (this.$refs.modalLeftTopCol.getBoundingClientRect().height)) + 'px';
      },
      showProcessModal: function (index) {
        //  
        this.selectedEventIndex = index;
        if(this.sensorEvents[index].sensor == undefined) {
          // 
          this.sensorEvents[index].sensor = this.sensorDetail;
        }
        this.sensorEvent = this.sensorEvents[index];
        this.sensorEventModal = true;
      },
      getWebsocketPush: function(event) {
        // ws
        // 
        this.updateEventList(event);
        if(event.eventType === 'GW_OP_DUTY_ANSWER') {
          // 
          this.loadCheckDutyList();
          this.checkDutyLoading = false;
        } else {
          // 
          switch(event.eventType) {
            case 'GW_OP_MANUAL_ALARM':
              this.deviceDetail.stManualAlarm = 'Y';break;
            case 'GW_MANUAL_ALARM_OFF':
              this.deviceDetail.stManualAlarm = 'N';break;
            case 'GW_FIRE_ON':
              this.deviceDetail.stFire = 'Y';break;
            case 'GW_FIRE_OFF':
              this.deviceDetail.stFire = 'N';break;
            case 'GW_FAULT_ON':
              this.deviceDetail.stFault = 'Y';break;
            case 'GW_FAULT_OFF':
              this.deviceDetail.stFault = 'N';break;
            case 'GW_P_POWER_FAULT_ON':
              this.deviceDetail.stPPowerFault = 'Y';break;
            case 'GW_P_POWER_FAULT_OFF':
              this.deviceDetail.stPPowerFault = 'N';break;
            case 'GW_S_POWER_FAULT_ON':
              this.deviceDetail.stSPowerFault = 'Y';break;
            case 'GW_S_POWER_FAULT_OFF':
              this.deviceDetail.stSPowerFault = 'N';break;
            case 'GW_OFFLINE_ON':
              this.deviceDetail.stOffline = 'Y';break;
            case 'GW_OFFLINE_OFF':
              this.deviceDetail.stOffline = 'N';break;
            case 'GW_WIRE_BREAK_ON':
              this.deviceDetail.stWireBreak = 'Y';break;
            case 'GW_WIRE_BREAK_OFF':
              this.deviceDetail.stWireBreak = 'N';break;
          }
          // 
          if(this.deviceDetail.stManualAlarm === 'Y' || this.deviceDetail.stFire === 'Y') {
            // 
            this.deviceDetail.runningStatus = 'FIRE';
          } else if(this.deviceDetail.stFault === 'Y' || this.deviceDetail.stPPowerFault === 'Y'
            || this.deviceDetail.stSPowerFault === 'Y' || this.deviceDetail.stOffline === 'Y'
            || this.deviceDetail.stWireBreak === 'Y') {
            // 
            this.deviceDetail.runningStatus = 'FAULT';
          } else {
            // 
            this.deviceDetail.runningStatus = 'NORMAL';
          }
        }
      },
      updateEventList: function (event) {
        // pop
        let eventIndex = _.findIndex(this.deviceEventList, {eventId: event.eventId});
        if (eventIndex !== -1) {
          this.deviceEventList.splice(eventIndex, 1);
        }
        this.deviceEventList.splice(0, 0, event);
      },
      closeProcessModal: function () {
        // modal
        this.sensorEventModal = false
      },
      onEventUpdate: function(event){
      }
    },
    watch: {
      // modal
      isShow: function (val, oldVal) {
        if(val) {
          this.initData();
        }else {
          // modaltrue
          this.deviceDetail.sourceType = undefined;
          this.currentActiveTab = 'planForm';
          this.showCamera = false;
          this.type = 'device';
          this.tranceiverTabName = 'workingStatus';
          this.processComment = undefined;
          this.closeModal();
        }
      },
      deviceDetailModal: function (val, oldVal) {
        this.isShow = val;
      },
      cameraDetailModal: function(val, oldVal) {
        if(!val) {
          // 
          try{
            if(this.cameraType === 'YDPNP') {
              let remote = document.querySelector('#remote');
              if(remote !== null && remote !== undefined) {
                remote.ClearDevs();
              }
            }
          }
          catch(exception){
            console.log("");
          } finally {
            this.cameraType = undefined;
          }
        }
      }
    },
    components:{
      // 
      'SensorEventModal': SensorEventModal
    },
    mounted(){
      tryMove();
      //Vue.component('SensorEventModal', SensorEventModal);
    }
  }
</script>



